.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #1c1c21;
  border-radius: 8px;
  z-index: 1000;
  max-height: 90vh;
  overflow-y: auto;
  min-width: 320px;
  width: 720px;
  text-align: center;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close {
  position: absolute;
  top: 50px;
  right: 50px;
  cursor: pointer;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}
