body {
  background-color: #fff;
  overflow: hidden;
  height: 100vh;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  border: none;
  background: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: none;
}

a {
  text-decoration: none;
  color: inherit;
}

:focus {
  outline: none;
}

body {
  line-height: 1;
}

h1 {
  font-family: 'JetBrains Mono', monospace;
  color: #f2f2f3;
  font-size: 36px;
  line-height: 40px;
  font-weight: 700;
  margin-bottom: 20px;
}

*::-webkit-scrollbar {
  width: 8px;
}

*::-webkit-scrollbar-track {
  background: #2f2f37;
}

*::-webkit-scrollbar-thumb {
  background: #8585ad;
  border-radius: 4px;
}

*::-webkit-scrollbar-thumb:hover {
  background: #a0a0c0;
}

* {
  scrollbar-width: thin;
  scrollbar-color: #8585ad #2f2f37;
}
