.header {
  max-width: 1920px;
  width: 100%;
  height: 88px;
  margin: 0 auto;
  background-color: #1c1c21;
  display: flex;
  align-items: center;
}

.headerContainer {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
}

.profile {
  display: flex;
  justify-content: flex-end;
}

.menuItem {
  display: flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
  font-weight: 500;
  font-family: 'JetBrains Mono', monospace;
  font-size: 16px;
  color: #8585ad;
}

.menuItem:hover {
  text-decoration: none;
}

.nav {
  display: flex;
  gap: 16px;
}

.active {
  color: #ffffff;
}
